import './App.css';
import {styled} from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import React, {useState} from "react";
import {loadEmail, generateContinueUrl, updateJsonObjectData} from "./conf/confHelper";
import AuthCode from "react-auth-code-input";

styled(Paper)(({theme}) => ({
    padding: theme.spacing(1),
    color: theme.palette.text.secondary,
    boxShadow: "none",
}));


export interface AppProps {
    search: string
}

export interface SmallProps {
    email: String
}


export default function App(props: AppProps) {

    const {search} = props
    const CODE_LENGTH = new Array(6).fill(0);

    const searchParam = new URLSearchParams(search);

    const email: String = loadEmail(searchParam) || ""

    const [inputValue, setInputValue] = useState('');
    const [loadingCode, setLoadingCode] = useState(false);

    const handleInputChange = (value:any) => {
        value = value.replace(/[^0-9]/g, '');
        setInputValue(value);

        if (value.length === 6) {
            handleSubmit(value);
        }
        console.log(value)
    };


    const handleSubmit = (value: any) => {
        setLoadingCode(true)
        const updateJsonObject = updateJsonObjectData(value, searchParam);
        window.location.href = generateContinueUrl(searchParam, updateJsonObject)
    };



    return (
        <div
            style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                height: '100vh',
            }}
        >

            <h2 style={{
                color: "#009b4c",
                fontSize: "32px",
                fontWeight: "normal"
            }}>Verify your account</h2>

            <div
                style={{
                    border: '1px solid #E7E6E6',
                    padding: "20px 40px",
                    textAlign: "center",
                }}
            >

                <VerificationMessage email={email}/>

                <div>
                    <small style={{
                        marginTop: "5%",
                        fontWeight: "700",
                        fontSize: "12px",
                        marginBottom: "5%"
                    }}>
                        Enter the 6-digit code
                    </small>

                    <div>

                        <AuthCode
                            onChange={handleInputChange}
                            allowedCharacters='numeric'
                            autoFocus={true}
                            length={6}
                            disabled={loadingCode}
                            containerClassName={"parent-field"}
                            inputClassName={"inner-field"}
                        />


                    </div>
                </div>


            </div>
        </div>
    );
}


const VerificationMessage = (props: SmallProps) => {
    const {email} = props
    return (
        <div style={{
            marginBottom: "20px"
        }}>
            <small>
                A temporary verification code has been sent to <br/>{email}
            </small>
        </div>
    );
};

